<template>
  <div class="container">
    <form class="main">

      <button type="button" class="btn btn-link helpbtn" data-toggle="modal" data-target="#exampleModal">
        Ajuda
      </button>

      <p class="title">
        Calculadora de distribuição Binomial
      </p>

      <div class="form-row spc">

        <div class="col-4">
          <label for="vn" class="align">Valor de N:</label>
          <input type="text" class="form-control" id="vn" placeholder="Entre com o valor de N" v-model="n" @blur='test($event.target.value, "an");'>
        </div>

        <div class="col-4">
          <label for="vx" class="align">Valor de X:</label>
          <input type="text" class="form-control" id="vx" placeholder="Entre com o valor de X" v-model="x" @blur='test($event.target.value, "ax");'>
        </div>

        <div class="col-4">
          <label for="vp" class="align">Valor de P:</label>
          <input type="text" class="form-control" id="vp" placeholder="Entre com o valor de P" v-model="p" @blur='minmax($event.target.value, "ap");'>
        </div>

      </div>
      
      <div class="container">
        <div class="alert alert-danger spacing" role="alert" v-if='err.length'>
          Um ou mais campos estão com valor inválido!
        </div>

        <div class="alert alert-warning spacing" role="alert" v-if='err1'>
          O valor de N precisa ser igual ou maior que o valor de X!
        </div>

        <div class="alert alert-warning spacing" role="alert" v-if='err2'>
          O valor de N precisa ser maior ou igual a 1!
        </div>
      </div>
      

      <p v-if="blank" class="calc">Um ou mais campos estão em branco para a realização do cálculo</p>

      <div class="calc spc" v-if="ready">

        <p class="spacing ">Resultados:</p>

        <div class="row">

          <div class="col-sm-6">
            <div class="alert alert-dark" role="alert">
              Coeficiente Binomial Individual: {{indiv}}
            </div>
          </div>

          <div class="col-sm-6">
            <div class="alert alert-dark" role="alert">
              Coeficiente Binomial Acumulado: {{acum}}
            </div>
          </div>

        </div>

      </div>

    </form>

    <!-- Modal -->
    <div class="modal fade" id="exampleModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">Ajuda</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <p>
              Esta calculadora provê o cálculo de distribuições binomiais dada a entrada do usuário.
            </p>
            Para que o programa funcione da maneira correta, atente-se aos seguintes pontos:
            <ul>
              <li>
                Insira o valor de P sempre na versão decimal (não porcentagem).
              </li>
              <li>
                Os valores de N e X devem ser naturais, sendo que X deve ser menor ou igual a N.
              </li>
              <li>
                Os resultados serão exibidos quando todas as variáveis forem inseridas corretamente.
              </li>
            </ul>
            <p>
              Clique <a href="https://www.inf.ufsc.br/~andre.zibetti/probabilidade/binomial.html" target="_blank"> aqui</a> ara mais informações sobre o cálculo de distribuições binomiais.
            </p>
            <p>
              Trabalho desenvolvido para a disciplina de Probabilidade e Estatística 2019.2 por Gabriel Vassoler (1611100010).
            </p>
            <p>
              Professor: Leandro Bordin.
            </p>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-primary" data-dismiss="modal">Fechar</button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  computed:{
    indiv: function(){
      return this.bin(this.n ,this.x , this.p).toFixed(4);
    },

    acum: function(){
      let h = 0;
      for(var i = 0; i <= this.x; i++){
        h += this.bin(this.n ,i , this.p);
      }
      return h.toFixed(4);
    },
  
    err1: function(){
      if(this.err.length == 0 && this.x != '' && this.n != ''){
        if(Number(this.n) < Number(this.x)){
          return true;
        }else{
          return false;
        }
      }
      return false;
    },
    
    blank: function(){
      if(this.x == '' || this.n == '' || this.p == ''){
        return true;
      }
      return false;
    },

    err2: function(){
      if(!Number.isNaN(Number(this.n)) && this.n != ''){
        if(this.n < 1){
          return true;
        }else{
          return false;
        }
      }
      return false;
    },

    ready: function(){
      if(!this.err1 && !this.err2 && !this.blank && this.err.length == 0){
        return true;
      }else{
        return false;
      }
    }
  },
  data: function(){
    return {
      n: '',
      x: '',
      p: '', 
      err: []
    }
  },
  methods:{

    test(value, v) {
      if(Number.isNaN(Number(value))){
        this.err.push(v);
      }else{
        if(value < 0){
          this.err.push(v);
        }else{
          this.err = this.err.filter(function(handle){
          return handle != v;
        })
        }
      }
    },

    minmax(value, v){
      if((value < 0 || value > 1) || Number.isNaN(Number(value))){
        this.err.push(v);
      }else{
        this.err = this.err.filter(function(handle){
          return handle != v;
        })
      }
    },

    fat(v){
      if(v == 0)
        return 1
      else
        return this.fat(v-1)*v
    },

    bin(n, x, p){
      return (this.fat(n)/(this.fat(x)*(this.fat(n-x))))*(Math.pow(p,x)*(Math.pow((1-p),(n-x))))
    }
  }
}
</script>

